.foundation {
    width: 110px;
    height: 158px;
    padding: 25px 28px 34px 28px;
    border: 1px solid #FFFFFF;
    border-radius: 5px;
    font-family: 'Roboto Slab', serif;
    font-size: 75px;
    font-weight: bold;
    color: rgba(255, 255, 255, 0.5);
    position: relative;
}

.foundation::after {
    content: 'A';
}

.foundation__card {
    position: absolute;
    top: 0;
    right: 0;
    user-select: none;
}
